import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, LoadingController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-manage-city',
  templateUrl: './manage-city.page.html',
  styleUrls: ['./manage-city.page.scss'],
})
export class ManageCityPage implements OnInit {
  public filter_data:any;
  public btnValue : any = "Add City";

public addcity : any = {city:''}
public allCities: any ;

  constructor(public global:GlobalService , public apicall : ApicallService, public toast : ToastService , public route : Router, private loadingCtrl: LoadingController, private alertController: AlertController) { }

  ngOnInit() {
    this.getAllCities();
  }

 async addcities(){
    console.log(this.addcity);
    if(this.btnValue == 'Add City'){
      await this.apicall.api_adddCitites(this.addcity);
      this.addcity = {city:''};
      this.getAllCities();
    }
    else{
      this.showLoading();
    await setTimeout(() => {
        this.apicall.api_updateCitites(this.addcity);
        this.addcity = {city:''};
        this.getAllCities();
        this.btnValue = "Add City";
      }, 1000);
    }

  }

  async getAllCities(){
    await this.apicall.api_getCities();
    this.global.GetCities.subscribe(res =>{
      this.allCities = res;
    });
  }

  async update(item){
    this.showLoading();
    setTimeout(() => {
      this.btnValue = "Update City";
      this.addcity = item;
    }, 1000);
  }

  async delete(c_id){
    const x = {c_id:c_id};
  await this.apicall.api_deleteCitites(x)
 await this.getAllCities();
  }

  async showLoading() {
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 1000,
      spinner: 'circles',
    });

    loading.present();
  }

  async DeleteAlert(c_id) {
    const alert = await this.alertController.create({
        header: 'Are you sure to delete this City!',
        buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                handler: () => {}
            }, {
                text: 'OK',
                role: 'confirm',
                handler: () => {
                  console.log(c_id);
                  this.delete(c_id);
                }
            },
        ]
    });

    await alert.present();

    const {role} = await alert.onDidDismiss();
}

}
